$global-font-icon: 'social';
@font-face {
  font-family: $global-font-icon;
  src:url("../assets/fonts/myicons.eot");
  src:url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"),
    url("../assets/fonts/myicons.woff") format("woff"),
    url("../assets/fonts/myicons.ttf") format("truetype"),
    url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: $global-font-icon;

  &:before {
    /* Some common icon stuff */
  }

  &.icon--arrow {
    &:before {
      content: $icon-arrow;
    }
  }
  &.icon--desktop {
    &:before {
      content: $icon-desktop;
    }
  }
  &.icon--idea {
    &:before {
      content: $icon-idea;
    }
  }
  &.icon--list {
    &:before {
      content: $icon-list;
    }
  }
  &.icon--performance {
    &:before {
      content: $icon-performance;
    }
  }
  &.icon--rocket {
    &:before {
      content: $icon-rocket;
    }
  }
  &.icon--value {
    &:before {
      content: $icon-value;
    }
  }

}